.gallery-section {
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 0 auto;
}

.carousel-img {
  margin: 0 15px;
  overflow: "hidden";
}

.carousel-img img {
  width: 100%;
}

@media (min-width: 768px) {
  .gallery-section {
    width: 70%;
    max-width: 1024px;
    padding: 80px 0;
  }

  .gallery {
    width: 70%;
    max-width: 1024px;
    /* grid-template-columns: auto auto auto; */
  }

  .gallery-test {
    flex-direction: row;
  }
}